import React, { useState } from 'react';
import './SkinTypeAnalyzer.css';

function determineSkinType(characteristics) {
  const { density, texture, pores, sebum, shine, wrinkles, age, skinColor } = characteristics;

  if (
    density === 'висока' &&
    texture === 'груба' &&
    pores === 'розширені' &&
    sebum === 'підвищене' &&
    shine === 'присутній' &&
    wrinkles === 'відсутні'
  ) {
    return 'Жирна шкіра';
  }

  if (
    density === 'тонка' &&
    texture === 'ніжна' &&
    pores === 'дрібні' &&
    sebum === 'знижене' &&
    shine === 'відсутній' &&
    wrinkles === 'присутні'
  ) {
    return 'Суха шкіра';
  }

  if (
    density === 'нормальна' &&
    texture === 'матова' &&
    pores === 'дрібні' &&
    sebum === 'умірене' &&
    shine === 'слабкий' 
    
  ) {
    return 'Нормальна шкіра';
  }

  if ((density === 'висока' && sebum === 'підвищене') || (density === 'нормальна' && sebum === 'підвищене')) {
    return 'Комбінована шкіра';
  }

  return 'Комбінована шкіра';
}

function getSkinRecommendations(skinType) {
  switch (skinType) {
    case 'Жирна шкіра':
      return [
        'Комбінована чистка обличчя',
        'Безін\'єкційна мезотерапія',
        'Біоревіталізація',
        'Хімічний пілінг',
        'Біодермабразія',
        'Фототерапія',
        'Фракційний лазер',
      ];
    case 'Суха шкіра':
      return [
        'Ультразвукова чистка обличчя',
        'Поверхневий пілінг',
        'Безін\'єкційна мезотерапія',
        'Киснева мезотерапія',
        'Hydrafacial',
        'Біоревіталізація',
        'Альгінатні маски',
      ];
    case 'Комбінована шкіра':
      return [
        'Комбінована чистка обличчя',
        'Пілінги',
        'Безін\'єкційна мезотерапія',
        'Неінвазивна карбокситерапія',
        'Біоревіталізація',
        'Фототерапія',
      ];
    case 'Нормальна шкіра':
      return [
        'Ультразвукова чистка обличчя',
        'Киснева мезотерапія',
        'Мікрострумова терапія',
        'Неінвазивна карбокситерапія',
        'Фракційна мезотерапія',
        'Поживні маски',
      ];
    default:
      return [];
  }
}

function SkinTypeAnalyzer() {
  const [characteristics, setCharacteristics] = useState({
    density: '',
    texture: '',
    pores: '',
    sebum: '',
    shine: '',
    wrinkles: '',
    age: '',
    skinColor: '',
  });

  const [result, setResult] = useState('');
  const [recommendations, setRecommendations] = useState([]);

  const handleChange = (e) => {
    const { name, value } = e.target;
    setCharacteristics((prevCharacteristics) => ({
      ...prevCharacteristics,
      [name]: value,
    }));
  };

  const analyzeSkinType = () => {
    const type = determineSkinType(characteristics);
    const recommendations = getSkinRecommendations(type);
    setResult(type);
    setRecommendations(recommendations);
  };

  return (
    <div className="skin-analyzer-container">
      <h2>Дайте відповіді на питання</h2>
      <div>
        <label>
          Щільність шкіри:
          <select name="density" value={characteristics.density} onChange={handleChange}>
            <option value="">Оберіть</option>
            <option value="висока">Висока</option>
            <option value="тонка">Тонка</option>
            <option value="нормальна">Нормальна</option>
          </select>
        </label>
      </div>
      <div>
        <label>
          Текстура шкіри:
          <select name="texture" value={characteristics.texture} onChange={handleChange}>
            <option value="">Оберіть</option>
            <option value="груба">Груба</option>
            <option value="ніжна">Ніжна</option>
            <option value="матова">Матова</option>
          </select>
        </label>
      </div>
      <div>
        <label>
          Розмір пор:
          <select name="pores" value={characteristics.pores} onChange={handleChange}>
            <option value="">Оберіть</option>
            <option value="розширені">Розширені</option>
            <option value="дрібні">Дрібні</option>
          </select>
        </label>
      </div>
      <div>
        <label>
          Кількість себуму:
          <select name="sebum" value={characteristics.sebum} onChange={handleChange}>
            <option value="">Оберіть</option>
            <option value="підвищене">Підвищене</option>
            <option value="знижене">Знижене</option>
            <option value="умірене">Умірене</option>
          </select>
        </label>
      </div>
      <div>
        <label>
          Наявність блиску:
          <select name="shine" value={characteristics.shine} onChange={handleChange}>
            <option value="">Оберіть</option>
            <option value="присутній">Присутній</option>
            <option value="відсутній">Відсутній</option>
            <option value="слабкий">Слабкий</option>
          </select>
        </label>
      </div>
      <div>
        <label>
          Зморшки:
          <select name="wrinkles" value={characteristics.wrinkles} onChange={handleChange}>
            <option value="">Оберіть</option>
            <option value="присутні">Присутні</option>
            <option value="відсутні">Відсутні</option>
            <option value="поступові">Поступові</option>
          </select>
        </label>
      </div>
      <div>
        <label>
          Вік:
          <select name="age" value={characteristics.age} onChange={handleChange}>
            <option value="">Оберіть</option>
            <option value="молодий">Молодий</option>
            <option value="середній">Середній</option>
            <option value="похилого віку">Похилого віку</option>
          </select>
        </label>
      </div>
      <div>
        <label>
          Колір шкіри:
          <select name="skinColor" value={characteristics.skinColor} onChange={handleChange}>
            <option value="">Оберіть</option>
            <option value="світлий">Світлий</option>
            <option value="середній">Середній</option>
            <option value="темний">Темний</option>
          </select>
        </label>
      </div>
      <button onClick={analyzeSkinType}>Визначити тип шкіри</button>
      {result && <p>Результат: {result}</p>}
      {recommendations.length > 0 && (
        <>
          <h3>Рекомендації для процедур:</h3>
          <ul>
            {recommendations.map((procedure, index) => (
              <li key={index}>{procedure}</li>
            ))}
          </ul>
        </>
      )}
    </div>
  );
}

export default SkinTypeAnalyzer;
