import axios from "axios";
import React, { useEffect, useState } from "react";
import { useLocation } from "react-router-dom";
import Loader from "../components/Loader";
import NewsList from "../components/NewsList";
import { Spring } from "react-spring/renderprops";

const News = () => {
   const [news, setNews] = useState([]);
   const [term, setTerm] = useState("");
   const [loading, setLoading] = useState(true);
   const location = useLocation();

   const search = (items, term) => {
      if (term.length === 0) {
         return items;
      }

      return items.filter((item) => {
         return item.title && item.title.toLowerCase().indexOf(term.toLowerCase()) > -1;
      });
   };

   useEffect(() => {
      const fetchData = async () => {
         const response = await axios.get(
            "https://react-news-cdea5.firebaseio.com/newslist.json"
         );

         if (!response.data) {
            setLoading(false);
            return;
         }

         const keyArr = [];
         const promisesArr = Object.keys(response.data).map((key) => {
            keyArr.push({ key });
            return axios.get(
               `https://react-news-cdea5.firebaseio.com/newslist/${key}.json`
            );
         });

         const values = await Promise.all(promisesArr);
         const newArr = values.map((item) => item.data);
         newArr.forEach((v, i) => (v.key = keyArr[i].key));
         setLoading(false);
         setNews(newArr);
      };
      fetchData();
   }, []);

   useEffect(() => {
     
      const searchParam = new URLSearchParams(location.search).get("keyword");
      setTerm(searchParam || ""); 
   }, [location.search]);

   const removeItem = async (id, key) => {
      const newArr = [...news];
      const result = newArr.filter((item) => item.id !== id);
      await axios.delete(
         `https://react-news-cdea5.firebaseio.com/newslist/${key}.json`
      );
      setNews(result);
   };

   const addItem = async (title, address, phone, price, date) => {
      const newItem = { title, address, phone, price, date, id: news.length + 1, show: false };
      const oldArray = [...news];
      oldArray.push(newItem);
      await axios.post(
         "https://react-news-cdea5.firebaseio.com/newslist.json",
         newItem
      );
      setNews(oldArray);
   };

   const onSearchChange = (term) => {
      setTerm(term);
   };

   const successNewsItem = (id, key) => {
      const newArr = [...news];
      newArr.forEach(async (item) => {
         if (item.id === id) {
            item.show = !item.show;
            await axios.patch(
               `https://react-news-cdea5.firebaseio.com/newslist/${key}.json`,
               { show: item.show }
            );
         }
      });
      setNews(newArr);
   };

   const visibleItems = search(news, term);

   return (
      <div className="page">
         <h1>Послуги</h1>

         {loading ? (
            <Loader />
         ) : (
            <Spring from={{ opacity: 0 }} to={{ opacity: 1 }}>
               {(props) => (
                  <div style={props}>
                     <NewsList
                        removeItem={removeItem}
                        newsList={visibleItems}
                        onSearchChange={onSearchChange}
                        addItem={addItem}
                        successNewsItem={successNewsItem}
                     />
                  </div>
               )}
            </Spring>
         )}
      </div>
   );
};

export default News;
