import React, { useState } from "react";
import { TextField, Button } from "@material-ui/core";

const CreateNews = (props) => {
   const [form, setForm] = useState({
      title: "",
      address: "", 
      phone: "",   
      price: "",   
      date: "",
   });

   const changeHandler = (e) => {
      const nowDate = new Date().toLocaleDateString();
      setForm({ ...form, [e.target.id]: e.target.value, date: nowDate });
   };

   const submitHandler = () => {
      if (form.title.trim() !== "" && form.address.trim() !== "") {
         props.addItem(form.title, form.address, form.phone, form.price, form.date);
      }
   };

   return (
      <div>
         <h2>Додати послугу</h2>
         <TextField
            id="title"
            label="Заголовок"
            placeholder="Заголовок"
            fullWidth
            margin="normal"
            InputLabelProps={{
               shrink: true,
            }}
            variant="filled"
            onChange={changeHandler}
         />
         <TextField
            id="address"
            label="Адреса"  
            placeholder="Адреса"
            fullWidth
            margin="normal"
            InputLabelProps={{
               shrink: true,
            }}
            variant="filled"
            onChange={changeHandler}
         />
         <TextField
            id="phone"
            label="Телефон"
            placeholder="Телефон"
            fullWidth
            margin="normal"
            InputLabelProps={{
               shrink: true,
            }}
            variant="filled"
            onChange={changeHandler}
         />
         <TextField
            id="price"
            label="Ціна"
            placeholder="Ціна"
            fullWidth
            margin="normal"
            InputLabelProps={{
               shrink: true,
            }}
            variant="filled"
            onChange={changeHandler}
         />
         <Button variant="contained" onClick={submitHandler}>
            Створити
         </Button>
      </div>
   );
};

export default CreateNews;
